.main-table {
  margin: 20px auto;
}
.table-head {
  background-color: #777777;
  color: white;
}
.table-head-item th {
  padding: 14px;
}

.table-body-item td {
  margin: 4px 0px;
  padding: 10px;
  border-bottom: 1px solid white;
  background-color: #e8eaf5;
}
.table-body-button {
  background-color: transparent;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex-start {
  display: flex;
  justify-content: start;
  align-items: center;
}

.category-icon {
  margin-right: 4px;
}
